body {
    max-width: 600px;
    margin-inline: auto;
    background-color: chocolate;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

* {
  box-sizing: border-box;
}

img {
  display: block;
}

.headline {
  text-align: center;
}

nav {
  display: flex;
  flex-direction: row;
  gap: 0.5em;
  justify-content: center;
}

.menubtn {
  transition: 50ms;
  margin-bottom: 10px;
  border: 1x solid black;
  padding: 0.5em 1em;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
}

.active-btn {
  background-color: chocolate;
}

#content {
  border: 1px solid black;
  padding: 1rem;
}

.hamburger-img {
  width: 100%;
}

.scale-in-center {
  -webkit-animation: scale-in-center 0.5s ease-in-out both;
          animation: scale-in-center 0.5s ease-in-out both;
}

.scale-out-center {
  -webkit-animation: scale-out-center 0.5s ease-in-out both;
    animation: scale-out-center 0.5s ease-in-out both;
}

